.floatingButton {
	animation: wiggle 4s 2s infinite;
	font-size: 4rem;
}

.hiddenSubtitles {
	display: none;
}

.arrowContainer {
	width: 100%;
	text-align: center;
	position: absolute;
	top: 28%;
}

@keyframes wiggle {
	5%,
	50% {
		transform: scale(1);
	}
	10% {
		transform: scale(0.9);
	}
	15% {
		transform: scale(1.15);
	}
	20% {
		transform: scale(1.15) rotate(-5deg);
	}
	25% {
		transform: scale(1.15) rotate(5deg);
	}
	30% {
		transform: scale(1.15) rotate(-3deg);
	}
	35% {
		transform: scale(1.15) rotate(2deg);
	}
	40% {
		transform: scale(1.15) rotate(0);
	}
}

@media only screen and (max-width: 800px) {
	.floatingTitles {
		display: none;
	}

	.hiddenSubtitles {
		display: block;
	}

	.arrowContainer {
		top: 10% !important;
	}
}
